export default {
	data() {
		return {
			visible: false,
			list: [],
			wrapperClosable: true,
			callback: null,
			beforeClose: null,
			showCancel: true,
			cancelText: null
		};
	},

	methods: {
		open({
			list = [],
			wrapperClosable = true,
			callback,
			beforeClose,
			cancelText = '取消',
			showCancel = true
		}) {
			this.wrapperClosable = wrapperClosable;
			this.list = list;
			this.callback = callback;
			this.beforeClose = beforeClose;
			this.cancelText = cancelText;
			this.showCancel = showCancel;

			this.visible = true;
		},

		close(action) {
			const done = () => {
				this.visible = false;

				if (this.callback) {
					this.callback({ action });
				}
			};

			if (this.beforeClose) {
				this.beforeClose({ action, done });
			} else {
				done();
			}
		},

		select(index, item) {
			if (item.disabled) {
				return false;
			}

			if (item.click) {
				item.click(() => {
					this.visible = false;
				});
			} else {
				this.close(index);
			}
		},

		onEvent(item, name) {
			if (item[name]) {
				item[name]();
			}
		}
	}
};
